<template>
    <div style="display: flex;flex-direction: column;align-items: center;width: 890px;text-align: center;margin: 0 auto;">
        <div>
            <div style="font: var(--td-font-headline-medium);">{{ data.title }}</div>
            <div style="font: var(--td-font-body-small);font-size: 16px;padding-top: 8px;">{{ data.sender }}</div>
        </div>
        <div style="padding: 56px 0px;font: var(--td-font-body-large)">
            <div>{{ data.body }}</div>
        </div>
        <div style="width: 560px;">
            <t-button size="large" block></t-button>
            <!-- <t-button size="large"></t-button> -->
        </div>
    </div>
</template>


<script>
import * as api from "../../components/config/api.js";
import * as config from "@/components/config.js"; // for testing, replace with "./config.js" for production server. ";
export default {
    name: 'MessageDetails',
    data() {
        return {
            data:{
                id: 1,
                title: '',
                sender: '',
                body: 'iiaiiui',
                date: '2021-07-16 10:10:10',
                status: false,
            }
        }
    },
    mounted() {
        this.$emit("mounted") 
        var mid = this.getUrlParam("mid")
        mid != false ? console.log("id"+mid+"") : console.log("id")
    },

    methods: {
        /**
        * @getUrlParam
        * @desc 
        * @param id 
        */
        getUrlParam(variable, url = window.location.search.substring(1)) {
            var vars = url.split("&");
            for (var i = 0; i < vars.length; i++) {
                var pair = vars[i].split("=");
                if (pair[0] == variable) { return pair[1]; }
            }
            return (false);
        },
    },

}
</script>
  
